* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 10px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
}

a:hover {
  text-decoration: none;
}

a,
a:visited {
  /* color: inherit; */
  text-decoration: none;
}

ul li {
  list-style: none;
}

button {
  padding: 0;
  border: 0;
  cursor: pointer;
  color: inherit;
}

// --------------- FONTS -----------------------------------------

/* noto-sans-regular - latin_cyrillic */
@font-face {
  font-family: "Noto Sans";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../public/fonts/noto-sans-v27-latin_cyrillic-regular.woff2") format("woff2"),
    /* Chrome 26+, Opera 23+, Firefox 39+ */ url("../public/fonts/noto-sans-v27-latin_cyrillic-regular.woff")
